<script>
  /**
   *   
   *   Bus.$emit('openUploader', {}) 
   *   Bus.$on('fileAdded', fn); 
   *            Bus.$on('fileSuccess', fn); 
   */
import $ from 'jquery';
import SparkMD5 from 'spark-md5';
import uploader from 'vue-simple-uploader';
// import Bus from '../../../../assets/js/bus';
import { fileMerge } from '@/apis/fileuploader';
import { merge } from 'lodash';
import { ACCEPT_CONFIG } from './config';
let type = 1;
  export default {
    props: {
      type: {
        type: Number,
        required: false
      }
    },
    data() {
      return {
        options: {
          //  URL
          target: '/system/file/upload',
          //
          chunkSize: 5 * 1024 * 1000,
          //file
          fileParameterName: 'file',
          //
          //simultaneousUploads: 1,
          //
          maxChunkRetries: 2,
          // 
          //chunkRetryInterval: 5000,
          //
          testChunks: true,
          // 
          checkChunkUploadedByResponse: function (chunk, message) {
            let objMessage = JSON.parse(message);
            if (objMessage.exists) {
              return true;
            }
            // if (objMessage.skipUpload) {
            //   return true;
            // }
            return (objMessage.uploaded || []).indexOf(chunk.offset + 1) >= 0
          },
          headers: {
            'Authorization-Token': '',
          },
          // 
          query: { upload_token: 'my_token' }
        },
        attrs: {
          accept: ACCEPT_CONFIG.getAll()
        },
        panelShow: false,   //panel
        collapse: false
      }
    },
    mounted() {
      //
      // Bus.$on('openUploader', query => {
      //   this.params = query || {};
      //   this.options.headers.Authorization = 'Bearer ' + query.token


      //   if (this.$refs.uploadBtn) {
      //     $("#global-uploader-btn").click();
      //   }
      // });
    },
    computed: {
      //Uploader
      uploader() {
        return this.$refs.uploader.uploader;
      }
    },
    methods: {
      getUrl() {
        return `/lightning-web/upload/uploadChunk?type=${this.type}`
      },
      openUploader() {
        if (this.$refs.uploadBtn) {
          document.getElementById("global-uploader-btn").click();
        }
      },
      test() {
        alert();
      },
      onFileAdded(file) {
        this.panelShow = true;
        this.computeMD5(file);

        // Bus.$emit('fileAdded');
      },
      //file-progress
      onFileProgress(rootFile, file, chunk) {
        console.log(` ${file.name}chunk${chunk.startByte / 1024 / 1024} ~ ${chunk.endByte / 1024 / 1024}`)
      },
      onFileSuccess(rootFile, file, response, chunk) {

        let res = JSON.parse(response);

        // TODO   :200Uploader
        // if (!res.result) {
        //   this.$message({message: res.message, type: 'error'});
        //   // 
        //   this.statusSet(file.id, 'failed');
        //   return
        // }
        // 
        if (res) {
          // 
          this.statusSet(file.id, 'merging');
          let param = {
            'filename': rootFile.name,
            'identifier': rootFile.uniqueIdentifier,
            'totalSize': rootFile.size,
            'type': type
          }
          fileMerge(param).then(res => {
            // 
            // Bus.$emit('fileSuccess');

            this.statusRemove(file.id);
          }).catch(e => {
            console.log(",,:", file.name)
            //,,,,
            file.retry();
          });

          // 
        } else {
          // Bus.$emit('fileSuccess');
          console.log('');
        }
      },
      onFileError(rootFile, file, response, chunk) {
        // this.$message({
        //   message: response,
        //   type: 'error'
        // })
      },

      /**
       * md5
       * @param file
       */
      computeMD5(file) {
        let fileReader = new FileReader();
        let time = new Date().getTime();
        let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice;
        let currentChunk = 0;
        const chunkSize = 10 * 1024 * 1000;
        let chunks = Math.ceil(file.size / chunkSize);
        let spark = new SparkMD5.ArrayBuffer();

        // "MD5"
        this.statusSet(file.id, 'md5');
        file.pause();

        loadNext();

        fileReader.onload = (e => {

          spark.append(e.target.result);

          if (currentChunk < chunks) {
            currentChunk++;
            loadNext();

            // MD5
            this.$nextTick(() => {
              $(`.myStatus_${file.id}`).text('MD5 ' + ((currentChunk / chunks) * 100).toFixed(0) + '%')
            })
          } else {
            let md5 = spark.end();
            this.computeMD5Success(md5, file);
            console.log(`MD5${file.name} \nMD5${md5} \n${chunks} :${file.size} ${new Date().getTime() - time} ms`);
          }
        });

        fileReader.onerror = function () {
          this.error(`${file.name}`)
          file.cancel();
        };

        function loadNext() {
          let start = currentChunk * chunkSize;
          let end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;

          fileReader.readAsArrayBuffer(blobSlice.call(file.file, start, end));
        }
      },

      computeMD5Success(md5, file) {
        // uploaderopts
        Object.assign(this.uploader.opts, {
          query: {
            ...this.params,
          }
        })

        file.uniqueIdentifier = md5;
        file.resume();
        this.statusRemove(file.id);
      },

      fileListShow() {
        let $list = $('#global-uploader .file-list');

        if ($list.is(':visible')) {
          $list.slideUp();
          this.collapse = true;
        } else {
          $list.slideDown();
          this.collapse = false;
        }
      },
      close() {
        this.uploader.cancel();

        this.panelShow = false;
      },

      /**
       * : 'md5''transcoding''failed'
       * @param id
       * @param status
       */
      statusSet(id, status) {
        let statusMap = {
          md5: {
            text: 'MD5',
            bgc: '#fff'
          },
          merging: {
            text: '',
            bgc: '#e2eeff'
          },
          transcoding: {
            text: '',
            bgc: '#e2eeff'
          },
          failed: {
            text: '',
            bgc: '#e2eeff'
          }
        }

        this.$nextTick(() => {
          $(`<p class="myStatus_${id}"></p>`).appendTo(`.file_${id} .uploader-file-status`).css({
            'position': 'absolute',
            'top': '0',
            'left': '0',
            'right': '0',
            'bottom': '0',
            'zIndex': '1',
            'line-height': 'initial',
            'backgroundColor': statusMap[status].bgc
          }).text(statusMap[status].text);
        })
      },
      statusRemove(id) {
        this.$nextTick(() => {
          $(`.myStatus_${id}`).remove();
        })
      },

      error(msg) {
        this.$notify({
          title: '',
          message: msg,
          type: 'error',
          duration: 2000
        })
      }
    },
    watch: {
    },
    destroyed() {},
    components: {}
  }
</script>

